import { ButtonAttributes, LegacyButtonVariants, variants } from "./legacyButtonVariants";

export const returnVariant = (variant: LegacyButtonVariants): ButtonAttributes => {
	switch (variant) {
		case "green":
			return variants.green;
		case "black":
			return variants.black;
		case "secondary":
			return variants.secondary;
		case "white":
			return variants.white;
		case "tertiary":
			return variants.tertiary;
		case "custom":
			return variants.custom;
		default:
			return variants.default;
	}
};
